jQuery( document ).ready( function( $ ) {
	let table = document.querySelector( '.wp-list-table' );
	if ( table ) {
		AdminColumns.HorizontalScrolling = new HorizontalScrolling( table );
		jQuery( '#acp_overflow_list_screen_table-yes' ).on( 'click', function() {

			if ( $( this ).is( ':checked' ) ) {
				AdminColumns.HorizontalScrolling.enable();
				AdminColumns.HorizontalScrolling.store();
			} else {
				AdminColumns.HorizontalScrolling.disable();
				AdminColumns.HorizontalScrolling.store();
			}

		} );
	}

} );

const CONST = {
	BODY_CLASS : 'acp-overflow-table',
	WRAPPER_CLASS : 'acp-hts-wrapper',
	OVERFLOW_CLASS : '-overflow',
	MORE_CLASS : '-more',
	LESS_CLASS : '-less'
};

class HorizontalScrolling {

	constructor( table ) {
		this.enabled = document.body.classList.contains( CONST.BODY_CLASS );
		this.table = table;
		this.wrapper = new TableWrapper( this.table );
		this.indicator = new ScrollIndicator( this.table, this.wrapper );

		if ( this.isEnabled() ) {
			this.wrapper.wrap();
			this.wrapper.enable();

			if ( ACP_Horizontal_Scrolling.hasOwnProperty( 'indicator_enabled' ) && ACP_Horizontal_Scrolling.indicator_enabled ) {
				this.indicator.enable();
			}
		}
	}

	isEnabled() {
		return this.enabled;
	}

	enable() {
		document.body.classList.add( CONST.BODY_CLASS );
		this.enabled = true;
		this.wrapper.wrap();
		this.wrapper.enable();
		this.wrapper.checkWrapperState();
		this.indicator.enable();
	}

	disable() {
		this.wrapper.disable();
		this.enabled = false;
		this.indicator.disable();
		document.body.classList.remove( CONST.BODY_CLASS );
	}

	checkWrapperState() {
		let total = this.table.scrollWidth;
		let width = this.table.offsetWidth;
		let scrollLeft = this.table.scrollLeft;
		let wrapper = document.querySelector( '.acp-table-wrapper' );

		wrapper.classList.remove( '-overflow', '-more', '-less' );

		if ( total > width ) {
			wrapper.classList.add( '-overflow' );

			if ( scrollLeft + width + 10 < total ) {
				wrapper.classList.add( '-more' );
			}

			if ( scrollLeft > 0 ) {
				wrapper.classList.add( '-less' );
			}
		}
	}

	store() {
		jQuery.post( ajaxurl, {
			action : 'acp_update_table_option_overflow',
			value : this.isEnabled(),
			layout : AC.layout,
			list_screen : AC.list_screen,
			_ajax_nonce : AC.ajax_nonce
		} );
	}
}

class TableWrapper {

	constructor( table ) {
		this.enabled = false;
		this.table = table;
		this.wrapper = null;
		this.initEvents();
	}

	enable() {
		this.enabled = true;
		this.checkWrapperState();
	}

	disable() {
		this.enabled = false;
	}

	initEvents() {
		this.table.addEventListener( 'scroll', () => {
			this.checkWrapperState();
		} );

		window.addEventListener( 'resize', () => {
			clearTimeout( this.timeout );

			this.timeout = setTimeout( () => {
				this.checkWrapperState();
			}, 100 )
		} );

	}

	wrap() {
		if ( this.table.parentElement.classList.contains( CONST.WRAPPER_CLASS ) ) {
			return;
		}

		this.wrapper = document.createElement( 'div' );
		this.wrapper.classList.add( CONST.WRAPPER_CLASS );
		this.table.parentNode.insertBefore( this.wrapper, this.table );
		this.wrapper.appendChild( this.table );
	}

	setOffset( percentage ) {
		let scrollableAreaWidth = this.table.scrollWidth - this.table.offsetWidth;
		let offsetX = (scrollableAreaWidth * (percentage / 100));

		this.table.scrollLeft = Math.round( offsetX );
	}

	getOffsetPercentage() {
		let scrollableAreaWidth = this.table.scrollWidth - this.table.offsetWidth;
		let offset = this.table.scrollLeft;
		let percentage = (offset / scrollableAreaWidth) * 100;

		return Math.round( percentage );
	}

	checkWrapperState() {
		if ( !this.enabled ) {
			return;
		}

		let total = this.table.scrollWidth;
		let width = this.table.offsetWidth;
		let scrollLeft = this.table.scrollLeft;

		this.wrapper.classList.remove( CONST.LESS_CLASS, CONST.MORE_CLASS, CONST.OVERFLOW_CLASS );

		if ( total > width ) {
			this.wrapper.classList.add( CONST.OVERFLOW_CLASS );

			if ( scrollLeft + width + 10 < total ) {
				this.wrapper.classList.add( CONST.MORE_CLASS );
			}

			if ( scrollLeft > 0 ) {
				this.wrapper.classList.add( CONST.LESS_CLASS );
			}
		}
	}

}

class ScrollIndicator {

	constructor( table, wrapper ) {
		this.table = table;
		this.wrapper = wrapper;

		this.initialX = 0;
		this.xOffset = 0;
		this.maxX = 0;
		this.tempX = 0;
		this.active = false;

		this.create();
		this.updateDraggerWidth();
		this.updateWidth();
	}

	disable() {
		this.element.style.display = 'none';
	}

	enable() {
		this.element.style.display = 'block';
	}

	hide() {
		this.element.classList.add( '-hidden' );
	}

	show() {
		this.element.classList.remove( '-hidden' );
	}

	create() {
		let element = document.createElement( 'div' );
		let dragger = document.createElement( 'div' );

		element.classList.add( 'acp-scrolling-indicator' );
		element.classList.add( '-start' );
		element.classList.add( '-hidden' );
		dragger.classList.add( 'acp-scrolling-indicator__dragger' );

		element.appendChild( dragger );
		document.body.appendChild( element );

		this.element = element;
		this.dragger = dragger;

		setTimeout( () => {
			this.maxX = element.clientWidth - dragger.offsetWidth;
		} );

		this.initEvents();
		this.disable();
		this.updateYPosition();
	}

	updateWidth() {
		this.element.style.width = (this.table.offsetWidth - 2) + 'px';
	}

	updateYPosition() {
		let tableBottom = this.table.getBoundingClientRect().bottom;
		let bottom = window.innerHeight - tableBottom;

		if ( bottom > this.element.offsetHeight ) {
			this.element.style.top = (window.innerHeight - bottom) + 'px';
		} else {
			this.element.style.top = 'inherit';
		}
	}

	updateDraggerWidth() {
		let percentage = Math.round( (this.table.offsetWidth / this.table.scrollWidth * 100) );

		if ( percentage === 100 ) {
			this.element.classList.add( '-fits' );
		} else {
			this.element.classList.remove( '-fits' );
		}

		this.dragger.style.width = `${percentage}%`;
		this.maxX = this.element.clientWidth - this.dragger.offsetWidth;
	}

	initEvents() {
		document.addEventListener( 'scroll', () => {
			this.updateYPosition();
			this.updateWidth();
		} );

		window.addEventListener( "touchmove", e => this.drag( e ), false );
		window.addEventListener( "mousemove", e => this.drag( e ), false );
		window.addEventListener( 'mouseup', e => this.dragEnd( e ) );

		this.dragger.addEventListener( "touchstart", e => this.dragStart( e ) );
		this.dragger.addEventListener( "touchend", e => this.dragEnd( e ) );

		this.dragger.addEventListener( "mousedown", e => this.dragStart( e ) );
		this.dragger.addEventListener( "mouseup", e => this.dragEnd( e ) );

		this.table.addEventListener( 'scroll', () => {
			this.updateIndicator();
		} );

		window.addEventListener( 'resize', () => {
			clearTimeout( this.timeout );

			this.timeout = setTimeout( () => {
				this.updateWidth();
				this.updateDraggerWidth();
			}, 100 );
		} );

		// Screen Option fix
		document.querySelectorAll( '#show-settings-link' ).forEach( el => {
			el.addEventListener( 'click', () => this.refreshPosition( 300 ) )
		} );
		document.addEventListener( 'click', () => this.refreshPosition() );
		setTimeout( () => { this.refreshPosition( 300 ) }, 100 );

		document.querySelectorAll( 'table .check-column input[type=checkbox]' ).forEach( el => {
			el.addEventListener( 'change', () => this.refreshPosition() );
		} );
	}

	refreshPosition( delay = 100 ) {

		setTimeout( () => {
			this.show();
			this.updateYPosition();
		}, delay );
	}

	updateIndicator() {
		if ( this.active ) {
			return;
		}

		let percentage = this.wrapper.getOffsetPercentage() / 100;
		let offset = Math.round( this.maxX * percentage );

		this.xOffset = offset;
		this.setTranslate( offset );
	}

	getCurrentOffset() {
		return this.xOffset;
	}

	dragStart( e ) {
		this.initialX = DragHelper.getX( e );
		this.active = true;
	}

	dragEnd() {
		if ( !this.active ) {
			return;
		}

		this.initialX = false;
		this.active = false;
		this.xOffset = this.tempX;

		AdminColumns.HorizontalScrolling.wrapper.setOffset( this.percentage );
	}

	drag( e ) {
		if ( this.active ) {
			e.preventDefault();

			let movementX = DragHelper.getX( e ) - this.initialX;
			let newXpos = this.getCurrentOffset() + movementX;

			if ( newXpos < 0 ) newXpos = 0;
			if ( newXpos > this.maxX ) newXpos = this.maxX;

			this.tempX = newXpos;
			this.percentage = (newXpos / this.maxX) * 100;

			this.setTranslate( newXpos );
			AdminColumns.HorizontalScrolling.wrapper.setOffset( this.percentage );
		}
	}

	setTranslate( xPos ) {
		if ( xPos === 0 ) {
			this.element.classList.add( '-start' )
		} else {
			this.element.classList.remove( '-start' )
		}
		this.dragger.style.left = `${xPos}px`;
	}

}

class DragHelper {

	static getX( e ) {
		if ( e.type === "touchmove" ) {
			return e.touches[ 0 ].clientX;
		} else {
			return e.clientX;
		}
	}

	static getY( e ) {
		if ( e.type === "touchmove" ) {
			return e.touches[ 0 ].clientY;
		} else {
			return e.clientY;
		}
	}
}